.Navigation{
    margin-bottom: 30px;
    position: relative;

    &::after{
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 1;
        box-shadow: var(--shadow);
    }
    
    &>div:first-child{
        display: flex;
        justify-content: space-between;

        img{
            width: 50px;
            height: 50px;
            align-self: center;
            margin-left: 30px;
        }
        

        nav{
            z-index: 10;
            ul{
                list-style-type: none;
                display: flex;
                justify-content: flex-end;
                margin: 0;
                padding: 10px 0;
                margin-right: 30px;
                
                li{
                    background-color: var(--main-color);
                    border-radius: 50%;
                    width: 32px;
                    height: 32px;
                    margin: 7px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border: 1px solid transparent;

                    .icon{
                        margin: 0;
                        color: white;
                        font-size: .8em;
                        max-width: 20px;
                    }

                    &:hover{
                        background-color: white;
                        border: 1px solid var(--main-color);
                        transition: .3s;

                        .icon{
                            color: var(--main-color);
                        }

                        .quizIcon{
                            filter: invert(1);
                        }
                    }
                }

                .logout{
                    &>li{
                        background-color: transparent;

                        &>.logoutIcon{
                            color: var(--danger);
                        }

                        &:hover{
                            border: 1px solid var(--danger);
                        }
                    }
                }
            }
        }
    }
}