.Issues{
    position: relative;
    box-shadow: var(--shadow);

    .step{
        background-color: var(--main-color);
        color: white;
        position: absolute;
        top: 0;
        right: 0;
        margin: 0;
        padding: 5px 10px;
        padding-left: 20px;
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 15% 100%);
    }

    h3 {
        margin: 10px;
        width: 70%;
    }

    .issueInfoIcon {
        margin-right: 10px;
        cursor: pointer;
        color: var(--main-grey);

        &:hover {
            color: var(--secondary-color);
        }
    }

    .content {
        background-color: var(--light-grey);
        padding: 15px;
        font-size: 14px;
    }

    form {
        border-bottom: 1px solid var(--light-grey);
        padding: 20px 0;
        margin: 0 10px;

        div {
            margin: 5px 0;

            input {
                cursor: pointer;
                height: 1em;
                width: 1em;
                margin-right: 10px;
            }

            label{
                cursor: pointer;
            }
        }
    }

    button{
        float: right;
        margin: 20px 10px;
        background-color: transparent;
        border: none;
        cursor: pointer;
        transition: .3s;

        &:focus{
            outline: none;
        }

        &:hover{
            letter-spacing: .1em;
            transition: .3s;
            .icon{
                color: var(--secondary-color);
            }
        }

        .icon{
            margin-left: 10px;
        }
    }
}